@use 'base/variables' as v;
@use 'sass:color';

.contenido-resumen {
    p {
        margin-bottom: 1rem;
    }

    span {
        color: v.$azul;
        font-weight: v.$bold;
        font-size: 2.4rem;
    }
}
.contenedor-servicio {
    border-bottom: 1px  solid v.$gris;
    padding: 3rem 0;
    &:last-of-type {
        margin-bottom: 3rem;
    }
}